import { scaleLinear, interpolateHcl, rgb, select } from 'd3';
const width = 960;
const height = 500;
window.onload = () => {
    const color = scaleLinear().domain([45, 75])
        .interpolate(interpolateHcl)
        .range([rgb("#007AFF"), rgb('#FFF500')]);
    const svg = select("body")
        .append("svg")
        .attr("width", width)
        .attr("height", height);
    const elLength = 500;
    const noteNumber = 60;
    const element = svg.append("g");
    element.append("rect")
        .attr("width", elLength)
        .attr("height", 20)
        .attr("rx", 5)
        .attr("ry", 5)
        .attr("x", 0)
        .attr("y", (noteNumber - 45) * 12)
        .attr("fill", color(noteNumber));
    element.append("text")
        .attr("x", 3)
        .attr("y", 15 + (noteNumber - 45) * 12)
        .text('test');

}
